width: 55%;
}
} */
@media screen and (max-width: 800px) {
  .details,
  .seller,
  .item,
  .coupon,
  .cart-footer {
    display: none;
  }
  .section {
    flex-direction: column;
  }
  .cart-left {
    width: 100%;
    border: none;
    box-sizing: border-box;
  }
  .offer {
    margin: 0px 10px;
    padding: 16px;
    box-sizing: border-box;
    width: 100%;
  }
  .select {
    display: none;
  }
  .product {
    width: 100%;
    margin-top: 15px;
  }
  .wish {
    margin: 0px 10px;
    box-sizing: border-box;
    width: 100%;
  }
  .cart-right {
    width: 100%;
    padding: 0;
  }
  .price {
    padding-left: 15px;
    box-sizing: border-box;
  }
  .place-btn {
    width: 300px;
    margin-left: 15px;
    box-sizing: border-box;
  }
  .price-flex {
    margin-left: 15px;
    box-sizing: border-box;
  }
}
