.footer {
  width: 100%;
  height: 500px;
  background: #fafbfc;
  margin-top: 50px;
  padding-top: 40px;
}
#section {
  width: 80%;
  height: 100%;
  margin: auto;
  /* border:1px solid black; */
  display: flex;
  justify-content: space-between;
}
#shopping {
  width: 15%;
  /* border:1px solid black; */
}
#space {
  margin-bottom: 40px;
}
.first h3 {
  margin-bottom: 25px;
  font-size: 15px;
}
.first p {
  margin: 5px 0px;
  font-size: 15px;
  font-weight: normal;
  color: #696b79;
}
#policies {
  width: 15%;
  /* border:1px solid black; */
}
#experience {
  width: 25%;
  /* border:1px solid black; */
}
#guarantee {
  width: 25%;
  /* border:1px solid black; */
}
#image {
  margin: 25px 0px;
}
#image img {
  width: 145px;
  height: 40px;
}
.logo img {
  margin-right: 10px;
  width: 25px;
  height: 25px;
}
.flex {
  display: flex;
  justify-content: space-evenly;
}
.flex1 {
  display: flex;
}
.footer2 {
  background-color: rgb(233, 229, 229);
  display: none;
}
.footer2 p {
  width: 30%;
  margin: auto;
  text-align: center;
  display: none;
}

@media (max-width: 1000px) {
  .footer {
    width: 100%;
    height: 0px;
    background: #fafbfc;
    margin-bottom: 40px;
    padding-top: 30px;
  }
  #section {
    display: none;
  }
  .footer2 {
    background-color: rgb(233, 229, 229);
    display: block;
  }
  .footer2 p {
    display: block;
    font-size: 13px;
  }
}
