* {
  margin: 0;
  padding: 0;
  font-family: Assistant, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica, Arial, sans-serif;
}

#text {
  font-size: 0.9rem;
  margin: 23px;
}

.single-main {
  width: 100%;
  /* height:1700px; */
  display: flex;
  justify-content: space-between;
}

.left {
  width: 55%;
  height: 1710px;
  /* border:1px solid red; */
  display: flex;
  flex-wrap: wrap;

  box-sizing: border-box;
  padding-left: 20px;
}

.img1 {
  width: 48%;
  height: 560px;
  margin: 5px;
  overflow: hidden;
}
.img2 {
  width: 48%;
  height: 560px;
  margin: 5px;
  overflow: hidden;
}
.img3 {
  width: 48%;
  height: 560px;
  margin: 5px;
  overflow: hidden;
}
.img4 {
  width: 48%;
  height: 560px;
  margin: 5px;
  overflow: hidden;
}
.img5 {
  width: 48%;
  height: 560px;
  margin: 5px;
  overflow: hidden;
}
.left > div > img {
  width: 100%;
  height: 100%;
}

.left img:hover {
  transform: scale(1.05);
  transition: all 0.2s ease-in-out;
}
.right {
  width: 44%;
  /* border: 1px solid red; */
}

.name {
  width: 95%;
  height: 120px;
  border-bottom: 1px solid #d0cdcd;
  margin-right: 10px;
}

.name p {
  color: grey;
  font-size: 1.2rem;
  margin-bottom: 20px;
}

.name span {
  border: 1px solid #eaeaec;
  color: grey;
  padding: 2px;
  font-size: 1rem;
  margin-bottom: 20px;
}

.name span img {
  height: 13px;
  width: 13px;
}

.size {
  width: 95%;
  padding-bottom: 25px;
  padding-top: 12px;
  border-bottom: 1px solid #d0cdcd;
}

.size > span:nth-child(1) {
  word-spacing: 5px;
}

.size > span:nth-child(1) b {
  color: orange;
  font-size: 1.15rem;
}

.size span:nth-child(1) strong {
  font-size: 1.25rem;
}

.size p:nth-child(2) {
  font-size: 0.9rem;
  font-weight: bold;
  margin-top: 8px;
  color: #03a685;
}

.size > div:nth-child(3) {
  width: 310px;
  /* border: 1px solid red; */
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  font-weight: bold;
}

.size div:nth-child(3) div:nth-child(1) {
  font-size: 0.9rem;
}

.size div:nth-child(3) div:nth-child(2) {
  font-size: 0.85rem;
  color: #ff3e6c;
  padding-top: 1px;
}

.size > div:nth-child(4) {
  width: 273px;
  /* border: 1px solid red; */
  display: flex;
  justify-content: space-between;
}

.size > div:nth-child(4) > div {
  width: 18%;
  height: 48px;
  border: 1px solid #bfc0c6;
  border-radius: 50%;
  font-size: 0.83rem;
  font-weight: bold;
  box-sizing: border-box;
}
.size > div:nth-child(4) > div:nth-child(1) {
  padding-left: 19px;
  padding-top: 12px;
}
.size > div:nth-child(4) > div:nth-child(2) {
  padding-left: 19px;
  padding-top: 12px;
}
.size > div:nth-child(4) > div:nth-child(3) {
  padding-left: 19px;
  padding-top: 12px;
}
.size > div:nth-child(4) > div:nth-child(4) {
  padding-left: 17px;
  padding-top: 12px;
}
.size > div:nth-child(4) > div:nth-child(5) {
  padding-left: 14px;
  padding-top: 12px;
}
.size > div:nth-child(4) > div:hover {
  border: 1px solid #ff3e6c;
}

.size > div:nth-child(5) {
  width: 60%;
  margin-top: 30px;
}

.addCart-btn {
  width: 51%;
  height: 52px;
  border-radius: 5px;
  background-color: #ff3e6c;
  font-size: 0.9rem;
  color: white;
  border: none;
  outline: none;
  margin-right: 15px;
}

.wish-btn {
  width: 45%;
  height: 52px;
  border-radius: 5px;
  background-color: white;
  font-size: 0.9rem;
  border: 1px solid #bfc0c6;
}

.wish-btn:hover {
  border: 1px solid black;
}

.options {
  margin: 30px 0px;
}

.options b {
  font-size: 1rem;
}

.options input {
  height: 38px;
  width: 40%;
  padding-left: 15px;
  margin-top: 17px;
  margin-bottom: 10px;
  font-size: 0.9rem;
  box-sizing: border-box;
  border: 1px solid #d0cdcd;
  border-radius: 5px;
  outline: none;
}

.options span {
  position: relative;
  left: -50px;
  font-size: 0.85rem;
  font-weight: bold;
  color: #ff3e6c;
}

.options > p:nth-child(6) {
  font-size: 0.83rem;
  color: #282c3f;
  margin-bottom: 25px;
}

.options > div:nth-child(7) p {
  line-height: 30px;
  color: #282c3f;
}

.offers {
  border-bottom: 1px solid #d0cdcd;
  width: 95%;
  padding-bottom: 25px;
}

.offers > div:nth-child(1) {
  font-size: 1rem;
  margin-bottom: 15px;
}

.offers > div:nth-child(2) > b:nth-child(1) {
  font-size: 1rem;
}

.offers > div:nth-child(2) > b:nth-child(2) {
  font-size: 1rem;
  color: orange;
}

.offers > div:nth-child(2) > ul {
  margin-left: 17px;
  margin-top: 7px;
  font-size: 0.9rem;
  line-height: 25px;
  color: #282c3f;
}

.offers > div:nth-child(2) > p:nth-child(4),
p:nth-child(7),
p:nth-child(10) {
  font-size: 0.85rem;
  font-weight: bold;
  color: #ff3e6c;
  margin: 10px 0px;
}

.offers > div:nth-child(2) > p:nth-child(5),
p:nth-child(8) {
  font-size: 1rem;
}

.products {
  width: 95%;
  margin-top: 25px;
  border-bottom: 1px solid #d0cdcd;
}

.products > div:nth-child(1) > b:nth-child(1) {
  font-size: 1rem;
}

.products > p:nth-child(2) {
  margin: 13px 0px;
  color: #282c3f;
}

.products > div:nth-child(3) > p:nth-child(2) {
  margin-top: 7px;
  margin-bottom: 10px;
  color: #282c3f;
}

.products > div:nth-child(4) > p:nth-child(2) {
  margin-top: 7px;
  color: #282c3f;
}

.products > div:nth-child(4) > p:nth-child(3) {
  margin-bottom: 10px;
  color: #282c3f;
}

#flex {
  width: 80%;
  height: 245px;
  /* border: 1px solid blue; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 13px;
  box-sizing: border-box;
}

#flex > div {
  width: 44%;
  height: 23%;
  border-bottom: 1px solid #ebe9e9;
  margin-bottom: 10px;
}

#flex > div > p:nth-child(1) {
  font-size: 0.75rem;
  color: #282c3f;
}

#flex > div > p:nth-child(2) {
  font-size: 1rem;
}

.products > div:nth-child(5) > p:nth-child(3) {
  font-size: 0.85rem;
  font-weight: bold;
  color: #ff3e6c;
  margin: 28px 0px;
}
.ratings {
  width: 95%;
  border-bottom: 1px solid #ebe9e9;
}
.ratings > div:nth-child(1) {
  margin-top: 20px;
  margin-bottom: 25px;
}

.flex-rating {
  width: 80%;
  /* height: 96px; */
  /* border-bottom:1px solid green; */
  display: flex;
}

.flex-rating > div:nth-child(1) > img:nth-child(2) {
  width: 25px;
  height: 25px;
}

.flex-rating > div:nth-child(1) > span:nth-child(1) {
  font-size: 3rem;
  margin-right: 7px;
}

.flex-rating > div:nth-child(1) > p:nth-child(3) {
  font-size: 0.9rem;
  margin-top: 10px;
  color: #282c3f;
}

.flex-rating > div:nth-child(2) {
  width: 1px;
  height: 115px;
  margin-left: 32px;
  margin-right: 40px;
  background-color: rgb(239, 237, 237);
}

/* .flex-rating>div:nth-child(3){
    display: flex;
} */
.flex-rating > div:nth-child(3) > div img {
  width: 17px;
  height: 17px;
}

.flex-rating > div:nth-child(3) > div:nth-child(1) {
  display: flex;
}

.flex-rating > div:nth-child(3) > div:nth-child(2) {
  display: flex;
}

.flex-rating > div:nth-child(3) > div:nth-child(3) {
  display: flex;
}

.flex-rating > div:nth-child(3) > div:nth-child(4) {
  display: flex;
}

.flex-rating > div:nth-child(3) > div:nth-child(5) {
  display: flex;
}

.index {
  height: 3px;
  width: 150px;
  margin: 10px 5px;
  display: flex;
}

/* 1st line */
.flex-rating
  > div:nth-child(3)
  > div:nth-child(1)
  > div:nth-child(3)
  > div:nth-child(1) {
  height: 4px;
  width: 40%;
  background-color: #03a685;
}

.flex-rating
  > div:nth-child(3)
  > div:nth-child(1)
  > div:nth-child(3)
  > div:nth-child(2) {
  height: 4px;
  width: 60%;
  background-color: #ede7e7;
}

/* 2nd line */
.flex-rating
  > div:nth-child(3)
  > div:nth-child(2)
  > div:nth-child(3)
  > div:nth-child(1) {
  height: 4px;
  width: 30%;
  background-color: #03a685;
}

.flex-rating
  > div:nth-child(3)
  > div:nth-child(2)
  > div:nth-child(3)
  > div:nth-child(2) {
  height: 4px;
  width: 70%;
  background-color: #ede7e7;
}

/* 3rd line */
.flex-rating
  > div:nth-child(3)
  > div:nth-child(3)
  > div:nth-child(3)
  > div:nth-child(1) {
  height: 4px;
  width: 20%;
  background-color: #03a685;
}

.flex-rating
  > div:nth-child(3)
  > div:nth-child(3)
  > div:nth-child(3)
  > div:nth-child(2) {
  height: 4px;
  width: 80%;
  background-color: #ede7e7;
}

/* 4th line */
.flex-rating
  > div:nth-child(3)
  > div:nth-child(4)
  > div:nth-child(3)
  > div:nth-child(1) {
  height: 4px;
  width: 10%;
  background-color: rgb(221, 159, 43);
}

.flex-rating
  > div:nth-child(3)
  > div:nth-child(4)
  > div:nth-child(3)
  > div:nth-child(2) {
  height: 4px;
  width: 90%;
  background-color: #ede7e7;
}

/* 5th line */
.flex-rating
  > div:nth-child(3)
  > div:nth-child(5)
  > div:nth-child(3)
  > div:nth-child(1) {
  height: 4px;
  width: 20%;
  background-color: rgb(186, 2, 2);
}

.flex-rating
  > div:nth-child(3)
  > div:nth-child(5)
  > div:nth-child(3)
  > div:nth-child(2) {
  height: 4px;
  width: 80%;
  background-color: #ede7e7;
}

.font-color {
  font-size: 0.85rem;
  color: #282c3f;
}
.ratings > div:nth-child(3) {
  margin-top: 42px;
  margin-bottom: 10px;
}
.ratings > div:nth-child(3) > span {
  font-size: 1rem;
  font-weight: bold;
}
.fitting {
  width: 43%;
  height: 135px;
}

.fitting > div:nth-child(2) {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.fitting > div:nth-child(4) {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
/* 1st line */
.fitting > div:nth-child(2) > div:nth-child(1) {
  display: flex;
  width: 59%;
  margin-top: 10px;
}

.fitting > div:nth-child(2) > div:nth-child(1) > div:nth-child(1) {
  height: 4px;
  width: 77%;
  background-color: #03a685;
}

.fitting > div:nth-child(2) > div:nth-child(1) > div:nth-child(2) {
  height: 4px;
  width: 23%;
  background-color: #ede7e7;
}
/* 2nd line */
.fitting > div:nth-child(4) > div:nth-child(1) {
  display: flex;
  width: 59%;
  margin-top: 10px;
}

.fitting > div:nth-child(4) > div:nth-child(1) > div:nth-child(1) {
  height: 4px;
  width: 83%;
  background-color: #03a685;
}

.fitting > div:nth-child(4) > div:nth-child(1) > div:nth-child(2) {
  height: 4px;
  width: 17%;
