}

.fitting > p:nth-child(1),
p:nth-child(3) {
  font-size: 0.9rem;
}

.fitting > div:nth-child(2) > p:nth-child(2) {
  font-size: 0.85rem;
  font-weight: bold;
}
.fitting > div:nth-child(4) > p:nth-child(2) {
  font-size: 0.85rem;
  font-weight: bold;
}
.fitting > p:nth-child(5) {
  font-size: 0.9rem;
  font-weight: bold;
  margin: 10px 0px;
}
.customer > p:nth-child(1),
p:nth-child(6) {
  margin: 15px 0;
  font-size: 1rem;
  font-weight: bold;
}
.customer img {
  width: 75px;
  height: 75px;
  margin-right: 5px;
}
.customer > div:nth-child(7) {
